@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
.login-background{height:100vh;background:#fff;background:linear-gradient(0deg, white 0%, #f9d946 100%);font-family:"Rubik",sans-serif}.login-background .container{display:grid;grid-template-columns:auto 45% auto;grid-gap:.8rem;padding-bottom:.9rem}.login-background .container img{grid-column:2;height:15rem;justify-self:center;margin-bottom:2rem}.login-background .container span{grid-column:2;font-size:30px;font-weight:800}.login-background .container .subtitle{grid-column:2;font-size:13px;font-weight:200}.login-background .container button{grid-column:2;margin-top:1.5rem;background-color:#000;border:none;color:#fff;height:2.5rem;width:100%;font-size:16px;font-family:"Rubik",sans-serif;cursor:pointer}.login-background .container input{grid-column:2;font-family:"Roboto",sans-serif;color:#333;width:100%;font-size:.8rem;grid-column:2;padding:1rem 1rem;border:none}.login-background .container .warning{color:red;text-align:center;font-size:small;font-weight:200}.Nombre{margin-left:5px}.grid{display:grid;grid-template-columns:auto 70% auto;grid-gap:.8rem;padding-bottom:.9rem}.grid .foils{grid-column:2;height:90vh;margin-top:1rem;overflow-y:scroll;scrollbar-color:#aaa #e0e0e0;scrollbar-width:thin}#logout{cursor:pointer}.CardContainer{display:grid;grid-template-columns:90% auto;grid-gap:.2rem;background-color:#f4f8fb;border-radius:.2rem;padding:.5rem .9rem;width:100%;max-width:35rem;margin-left:50px;box-shadow:0px 0px 5px gray;margin:1.2rem auto;cursor:pointer}.CardContainer #name,.CardContainer #foil{grid-column:1}.CardContainer img{grid-column:2;grid-row:1/3;text-align:center;margin-top:1rem}.CardDetailContainer{display:grid;grid-gap:.2rem;padding:.5rem .9rem;width:100%;max-width:35rem;margin-left:50px;margin:1.2rem auto}.CardDetailContainer .NoImage{margin:auto;width:100%;height:auto}.CardDetailContainer .DetailTitle{text-align:center;border-radius:.2rem;height:2.5rem;padding:.1rem;margin-bottom:.5rem;color:#000;font-weight:400;font-size:22px;box-shadow:1px 1px 3px #828788}.CardDetailContainer .Download{margin:1rem 30%;background-color:#d5dbdb;text-align:center;box-shadow:1px 1px 5px #828788;border-radius:.2rem;height:2.5rem;font-weight:600;font-size:1.2rem;cursor:pointer}.CardDetailContainer .base64Image{margin:auto;width:100%;height:auto}.CardDetailContainer a{color:#000;text-decoration:none;vertical-align:middle;margin-top:.1rem}.Nombre{margin-left:5px}.gridDetail{display:grid;grid-template-columns:auto 70% auto;grid-gap:.8rem;padding-bottom:.9rem}.gridDetail .foils{grid-column:2;height:90vh;margin-top:1rem;overflow-y:scroll;scrollbar-color:#aaa #e0e0e0;scrollbar-width:thin}.User-CardContainer{display:grid;grid-template-columns:10% 80% auto;grid-gap:.2rem;background-color:#f4f8fb;border-radius:.2rem;padding:.8rem .9rem;width:100%;max-width:35rem;margin-left:50px;box-shadow:0px 0px 5px gray;margin:1.2rem auto;cursor:pointer}.User-CardContainer #name,.User-CardContainer #foil{grid-column:2;font-size:18px;margin-top:.3rem}.User-CardContainer .user-icon-arrow{grid-column:3;grid-row:1/3;text-align:center;margin-top:.6rem}.User-CardContainer .user-icon{text-align:center;margin:.4rem 0rem 0rem .6rem}.lds-roller{width:80px;height:80px;text-align:center;margin:auto;margin-top:2rem}.lds-roller div{-webkit-animation:lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;-webkit-transform-origin:40px 40px;transform-origin:40px 40px}.lds-roller div:after{content:" ";display:block;position:absolute;width:7px;height:7px;border-radius:50%;background:#ffc919;margin:-4px 0 0 -4px}.lds-roller div:nth-child(1){-webkit-animation-delay:-0.036s;animation-delay:-0.036s}.lds-roller div:nth-child(1):after{top:63px;left:63px}.lds-roller div:nth-child(2){-webkit-animation-delay:-0.072s;animation-delay:-0.072s}.lds-roller div:nth-child(2):after{top:68px;left:56px}.lds-roller div:nth-child(3){-webkit-animation-delay:-0.108s;animation-delay:-0.108s}.lds-roller div:nth-child(3):after{top:71px;left:48px}.lds-roller div:nth-child(4){-webkit-animation-delay:-0.144s;animation-delay:-0.144s}.lds-roller div:nth-child(4):after{top:72px;left:40px}.lds-roller div:nth-child(5){-webkit-animation-delay:-0.18s;animation-delay:-0.18s}.lds-roller div:nth-child(5):after{top:71px;left:32px}.lds-roller div:nth-child(6){-webkit-animation-delay:-0.216s;animation-delay:-0.216s}.lds-roller div:nth-child(6):after{top:68px;left:24px}.lds-roller div:nth-child(7){-webkit-animation-delay:-0.252s;animation-delay:-0.252s}.lds-roller div:nth-child(7):after{top:63px;left:17px}.lds-roller div:nth-child(8){-webkit-animation-delay:-0.288s;animation-delay:-0.288s}.lds-roller div:nth-child(8):after{top:56px;left:12px}@-webkit-keyframes lds-roller{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-roller{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
